<template>
    <div id="blogHeader">
        <nav>
            <ul>
                <router-link to="/" exact>博客</router-link>
                <router-link to="/add" exact>写博客</router-link>
            </ul>
        </nav>
    </div>
</template>

<script>
export default {
    name:"BlogHeader"
}
</script>

<style scoped>
    ul{
        list-style-type: none;
        text-align: center;
        margin:0;
    }
    li{
        display: inline-block;
        margin:20px;
    }
    a{
        color:#fff;
        text-decoration: none;
        padding:12px;
        border-radius: 5px
    }
    nav{
        background-color: crimson;
        padding:30px 0;
    }
    .router-link-active{
        background-color: #fff;
        color:#ccc;
    }
</style>


