<template>
  <div class="AddBlog">
      <div id= "add" v-show="!isShow">
        <h1>添加博客</h1>
        <p>主题</p>
        <!-- required 表示表单为必填项 -->
        <input type="text" v-model="blog.title" required>
        <p>内容</p>
        <textarea v-model="blog.content"></textarea>
        <form action="">
        <p>分类：</p>
        <label >Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.category">
        <label >Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.category" >
        <label >Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.category">
        <label >React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.category">
        </form>

        <p>作者</p>
        <select v-model="blog.author">
          <option v-for="(author,index) in author" :key="index">{{author}}</option>
        </select>
        <p>
        <button @click.prevent="addSome">添加博客</button>
        </p>
      </div>  

      <h2 v-show="isShow">博客添加完成</h2>

      <div id="show" >
        <h3>博客主题</h3>
        <p>{{blog.title}}</p>
        <p>内容</p>
        <p>{{blog.content}}</p>
        <p>分类</p>
        <ul>
          <li v-for="(category,index) in blog.category" :key="index">{{category}}</li>
        </ul>
        <p>作者</p>
        <p>{{blog.author}}</p>
      </div>

  </div>
</template>

<script>
export default {
  name: 'AddBlog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        author:"",      
        category:[],
      },
      author:["lls","lsl","lll","sss"],
      isShow:false
    }
  },
  methods:{
    addSome:function(){
    this.axios.post('https://blog-practise.firebaseio.com/post.json',this.blog)
      .then((res) => console.log(res))
    this.isShow = !this.isShow
    }
   
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .AddBlog *{
    box-sizing: border-box;
  }
  .AddBlog{
    margin:0px auto;
    padding:20px auto;
    max-width: 600px;

  }
  h1{
    text-align: center
  }
  #add p{
    margin: 20px 0 10px 0 ;
  }
  #add input[type="text"],textarea,select{
    display: block;
    width:300px;
    height:40px;
    padding:8px
  }
  #add textarea{
    height:200px;
  }
  #add button{
    display: inline-block;
    height:40px;
    width:80px;
    background-color: crimson;
    color:#fff;
    border:none;
    border-radius: 5px;
  }
  #show{
    padding:10px 20px ;
    border:solid 1px #cccccc;
    margin-top:30px;
  }
  #show ul{
    list-style: none  
  }
  
</style>



