import ShowBlog from '../components/ShowBlog'
import AddBlog from '../components/AddBlog'
import SingleBlogs from '../components/SingleBlogs'

export default[
    {
        path:"/",
        component:ShowBlog
    },
    {
        path:"/add",
        component:AddBlog
    },
    {
        path:"/blog/:id",
        component:SingleBlogs,
        props: true
    }
]


