<template>
  <div id="ShowBlog" >
      <h1>展示博客</h1>
      <input type="text" placeholder="搜索" v-model="seach">
      <div v-test="'yes'" class="BlogContent" v-for="(blogCont,index) in seachBlogs" :key="index">
        <router-link :to="'/blog/' + blogCont.id"><h2>{{blogCont.title}}</h2></router-link>
        <article>{{blogCont.content}}</article>
      </div>
  </div>
</template>

<script>

export default {
  name: 'ShowBlog',
  data(){
      return {
        blogContent:[],
        seach:""
      }
  },
  created(){
    this.axios.get('https://blog-practise.firebaseio.com/post.json')
      .then((res) => {
          let blogs = []
          for(let key in res.data){
            res.data[key].id = key;
            blogs.push(res.data[key])
          }
          // console.log(blogs);
          this.blogContent = blogs
      })
  },
  computed:{
      "seachBlogs":function(){
          return this.blogContent.filter((el) => {
              return el.title.match(this.seach)
          })
      }
  }
}
</script>

<style >
a{
  text-decoration: none;
  color:#000
}
#ShowBlog{
    max-width: 600px;
    margin:0 auto;
}
#ShowBlog h1{
    text-align:center;
}
.BlogContent{
    background-color: #eee;
    margin:20px 0;
    padding:20px;
    box-sizing: border-box;
}
input[type="text"]{
  padding:8px;
  width:100%;
  box-sizing: border-box;
}


</style>



