<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <p>内容：</p>
        <article>{{blog.content}}</article>
        <ul>
            <li>作者:{{blog.author}}</li>
        </ul>
        <p>分类：</p>
        <ul>
            <li v-for="(category,index) in blog.category" :key = "index">{{category}}</li>
        </ul>
    </div>
</template>

<script>
export default {
    name:"single-blog",
    data(){
        return{
            blog:{}
        }
    },
    created(){
        this.axios.get('https://blog-practise.firebaseio.com/post/'+this.id+'.json')
                  .then(res =>{
                      this.blog = res.data
                  })
    },
    props:["id"]
}
</script>

<style scoped>
    #single-blog{
        max-width: 800px;
        margin: 0 auto;
        text-align: center;
        border: 1px solid #eee;
        background-color : darkgray;
        margin-top:20px;
        padding-bottom: 20px;
    }
    li{
        list-style: none;
    }
    article{
        padding:10px 0 20px 0
        ;
    }
</style>


